import { type ClassValue, clsx } from "clsx"
import { twMerge } from "tailwind-merge"

// Utility function untuk menggabungkan Tailwind classes dengan conditional logic
export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs)) // Menggabungkan dan merge Tailwind classes
}

// Interface untuk data survey yang akan diexport
interface SurveyData {
  rating: number;    // Rating dari 1-5
  comment: string;   // Komentar optional
  timestamp: string; // Waktu submit dalam format ISO
}

// Function untuk mengkonversi rating angka menjadi emoji
export function getRatingEmoji(rating: number): string {
  const emojis = {
    1: "üò°", // Sangat tidak puas
    2: "üòû", // Tidak puas  
    3: "üòê", // Netral
    4: "üôÇ", // Puas
    5: "üòÄ"  // Sangat puas
  };
  return emojis[rating as keyof typeof emojis] || "‚ùì"; // Return emoji atau tanda tanya jika rating tidak valid
}

// Function untuk mengkonversi rating angka menjadi text deskripsi
export function getRatingText(rating: number): string {
  const texts = {
    1: "Sangat Tidak Puas",
    2: "Tidak Puas",
    3: "Netral", 
    4: "Puas",
    5: "Sangat Puas"
  };
  return texts[rating as keyof typeof texts] || "Tidak Valid"; // Return text atau "Tidak Valid"
}

// Function untuk generate CSV content dari array survey data
export function generateCSV(data: SurveyData[]): string {
  // Header CSV dengan nama kolom
  const headers = [
    "No",           // Nomor urut
    "Rating",       // Rating dalam angka
    "Emoji",        // Emoji representation
    "Deskripsi",    // Text deskripsi rating
    "Komentar",     // Komentar user
    "Tanggal",      // Tanggal submit
    "Waktu"         // Waktu submit
  ];
  
  // Gabungkan header dengan koma sebagai separator
  let csvContent = headers.join(",") + "\n";
  
  // Loop through setiap data survey
  data.forEach((item, index) => {
    // Parse timestamp untuk mendapatkan tanggal dan waktu terpisah
    const date = new Date(item.timestamp);
    const tanggal = date.toLocaleDateString("id-ID"); // Format tanggal Indonesia
    const waktu = date.toLocaleTimeString("id-ID");   // Format waktu Indonesia
    
    // Bersihkan komentar dari karakter yang bisa merusak CSV (koma, newline, quotes)
    const cleanComment = item.comment
      .replace(/"/g, '""')    // Escape double quotes dengan double double quotes
      .replace(/\n/g, " ")    // Replace newline dengan spasi
      .replace(/\r/g, "");    // Remove carriage return
    
    // Buat array row data
    const row = [
      index + 1,                    // Nomor urut (mulai dari 1)
      item.rating,                  // Rating angka
      getRatingEmoji(item.rating),  // Emoji rating
      getRatingText(item.rating),   // Text deskripsi rating
      `"${cleanComment}"`,          // Komentar dalam quotes untuk handle koma
      tanggal,                      // Tanggal
      waktu                         // Waktu
    ];
    
    // Gabungkan row dengan koma dan tambahkan ke CSV content
    csvContent += row.join(",") + "\n";
  });
  
  return csvContent; // Return complete CSV string
}

// Function untuk format tanggal Indonesia yang lebih readable
export function formatIndonesianDate(dateString: string): string {
  const date = new Date(dateString);
  const options: Intl.DateTimeFormatOptions = {
    weekday: 'long',  // Nama hari lengkap
    year: 'numeric',  // Tahun 4 digit
    month: 'long',    // Nama bulan lengkap
    day: 'numeric',   // Tanggal
    hour: '2-digit',  // Jam 2 digit
    minute: '2-digit' // Menit 2 digit
  };
  
  return date.toLocaleDateString('id-ID', options); // Format sesuai locale Indonesia
}

// Function untuk validasi rating input
export function isValidRating(rating: any): rating is number {
  return typeof rating === 'number' && rating >= 1 && rating <= 5; // Check apakah rating valid (1-5)
}

// Function untuk generate random ID (untuk keperluan development)
export function generateId(): string {
  return Date.now().toString(36) + Math.random().toString(36).substr(2); // Generate unique ID dari timestamp dan random
}
