"use client"; // Directive untuk Next.js client component (diperlukan untuk useState dan event handlers)

import React, { useState } from "react";
import Link from "next/link";
import { getRatingEmoji } from "@/lib/utils";

// Interface untuk struktur data survey
interface SurveyFormData {
  rating: number | null; // Rating yang dipilih (1-5) atau null jika belum dipilih
  comment: string;       // Komentar optional dari user
}

// Component halaman survey untuk publik (non-admin)
export default function SurveyPage() {
  // State untuk menyimpan data form survey
  const [formData, setFormData] = useState<SurveyFormData>({
    rating: null,  // Initial rating belum dipilih
    comment: ""    // Initial comment kosong
  });
  
  // State untuk status loading saat submit
  const [isLoading, setIsLoading] = useState(false);
  
  // State untuk pesan sukses atau error
  const [message, setMessage] = useState("");
  
  // State untuk tipe pesan (success atau error)
  const [messageType, setMessageType] = useState<"success" | "error" | "">("");

  // Array emoji untuk rating dari 1-5
  const ratingEmojis = [
    { value: 1, emoji: "üò°", label: "Sangat Tidak Puas" },
    { value: 2, emoji: "üòû", label: "Tidak Puas" },
    { value: 3, emoji: "üòê", label: "Netral" },
    { value: 4, emoji: "üôÇ", label: "Puas" },
    { value: 5, emoji: "üòÄ", label: "Sangat Puas" }
  ];

  // Handler untuk mengubah rating yang dipilih
  const handleRatingChange = (rating: number) => {
    setFormData(prev => ({ ...prev, rating })); // Update rating di state
    setMessage(""); // Clear pesan error jika ada
  };

  // Handler untuk mengubah komentar
  const handleCommentChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setFormData(prev => ({ ...prev, comment: e.target.value })); // Update comment di state
  };

  // Handler untuk submit form survey
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault(); // Prevent default form submission behavior
    
    // Validasi: pastikan rating sudah dipilih
    if (formData.rating === null) {
      setMessage("Silakan pilih rating kepuasan Anda dengan mengklik emoji");
      setMessageType("error");
      return;
    }

    setIsLoading(true); // Set loading state
    setMessage("");     // Clear previous messages

    try {
      // Kirim data survey ke API endpoint menggunakan fetch
      const response = await fetch("/api/survey", {
        method: "POST",                                    // HTTP method POST
        headers: {
          "Content-Type": "application/json",              // Set content type JSON
        },
        body: JSON.stringify({                             // Convert data ke JSON string
          rating: formData.rating,
          comment: formData.comment.trim()                 // Trim whitespace dari comment
        }),
      });

      // Parse response JSON
      const result = await response.json();

      if (response.ok) {
        // Jika sukses, tampilkan pesan sukses dan reset form
        setMessage("Terima kasih! Survey Anda telah berhasil dikirim.");
        setMessageType("success");
        
        // Reset form data
        setFormData({
          rating: null,
          comment: ""
        });
      } else {
        // Jika gagal, tampilkan pesan error dari server
        setMessage(result.message || "Gagal mengirim survey. Silakan coba lagi.");
        setMessageType("error");
      }
    } catch (error) {
      // Handle network error atau error lainnya
      console.error("Error submitting survey:", error);
      setMessage("Terjadi kesalahan jaringan. Silakan coba lagi.");
      setMessageType("error");
    } finally {
      setIsLoading(false); // Reset loading state
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 py-8 px-4">
      <div className="max-w-2xl mx-auto">
        {/* Header dengan logo dan nama instansi */}
        <header className="text-center mb-8">
          {/* Logo PU placeholder */}
          <div className="w-20 h-20 bg-blue-600 rounded-full flex items-center justify-center mx-auto mb-4">
            <span className="text-white font-bold text-lg">PU</span>
          </div>
          
          {/* Nama instansi */}
          <h1 className="text-3xl font-bold text-gray-800 mb-2">
            Balai Jasa Konstruksi Wilayah III Jakarta
          </h1>
          
          {/* Subtitle */}
          <p className="text-lg text-gray-600">
            Survey Kepuasan Pelayanan
          </p>
        </header>

        {/* Form survey container */}
        <div className="bg-white rounded-lg shadow-lg p-8">
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Section untuk rating dengan emoji */}
            <div>
              <label className="block text-lg font-semibold text-gray-700 mb-4 text-center">
                Bagaimana tingkat kepuasan Anda terhadap pelayanan kami?
              </label>
              
              {/* Container untuk emoji rating buttons */}
              <div className="flex justify-center space-x-4 mb-4">
                {ratingEmojis.map((item) => (
                  <button
                    key={item.value}
                    type="button"                    // Prevent form submission
                    onClick={() => handleRatingChange(item.value)}
                    className={`
                      flex flex-col items-center p-4 rounded-lg border-2 transition-all duration-200
                      ${formData.rating === item.value 
                        ? 'border-blue-500 bg-blue-50 scale-110' 
                        : 'border-gray-200 hover:border-gray-300 hover:bg-gray-50'
                      }
                    `}
                    title={item.label}               // Tooltip text
                  >
                    <span className="text-4xl mb-2">{item.emoji}</span>
                    <span className="text-xs text-gray-600 text-center">
                      {item.label}
                    </span>
                  </button>
                ))}
              </div>
              
              {/* Tampilkan rating yang dipilih */}
              {formData.rating && (
                <p className="text-center text-gray-600">
                  Anda memilih: <span className="font-semibold">
                    {getRatingEmoji(formData.rating)} {ratingEmojis.find(r => r.value === formData.rating)?.label}
                  </span>
                </p>
              )}
            </div>

            {/* Section untuk komentar optional */}
            <div>
              <label htmlFor="comment" className="block text-lg font-semibold text-gray-700 mb-2">
                Komentar atau Saran (Opsional)
              </label>
              <textarea
                id="comment"
                value={formData.comment}
                onChange={handleCommentChange}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none"
                rows={4}
                placeholder="Berikan komentar atau saran untuk perbaikan pelayanan kami..."
                maxLength={500}                    // Batasi panjang komentar
              />
              <p className="text-sm text-gray-500 mt-1">
                {formData.comment.length}/500 karakter
              </p>
            </div>

            {/* Submit button */}
            <div className="text-center">
              <button
                type="submit"
                disabled={isLoading || formData.rating === null}
                className={`
                  px-8 py-3 rounded-lg font-semibold text-white transition-all duration-200
                  ${isLoading || formData.rating === null
                    ? 'bg-gray-400 cursor-not-allowed'
                    : 'bg-blue-600 hover:bg-blue-700 hover:scale-105'
                  }
                `}
              >
                {isLoading ? (
                  <span className="flex items-center">
                    <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                      <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                      <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                    </svg>
                    Mengirim...
                  </span>
                ) : (
                  "Kirim Survey"
                )}
              </button>
            </div>

            {/* Tampilkan pesan sukses atau error */}
            {message && (
              <div className={`
                p-4 rounded-lg text-center font-medium
                ${messageType === "success" 
                  ? "bg-green-100 text-green-800 border border-green-200" 
                  : "bg-red-100 text-red-800 border border-red-200"
                }
              `}>
                {message}
              </div>
            )}
          </form>
        </div>

        {/* Navigation back to home */}
        <div className="text-center mt-8">
          <Link 
            href="/" 
            className="inline-flex items-center text-blue-600 hover:text-blue-800 font-medium"
          >
            ‚Üê Kembali ke Halaman Utama
          </Link>
        </div>
      </div>
    </div>
  );
}
